.crumbs {
  margin: 1px;
  display: inline-flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  list-style-type: none;
  gap: 0.2rem;
}

.crumb {
  text-align: justify;
  user-select: none;
  margin: 2px 2px;
  font-size: 15px;
  width: 30px;
  display: block;
  padding: 0.3rem .3rem;
  text-decoration: none;
  border-radius: 0.1rem;
  border: 0.0625rem solid #d7d7d7; /* 1px */
  box-shadow: 6px 7px 8px -6px rgba(8, 8, 8, 0.7);
  color: rgb(250, 249, 249);
  background-color: rgb(13, 13, 14);
}

.crumb:hover, .crumb__active {
  text-align: center;
  user-select: none;
  margin: 20px 5px;
  font-size: 14px;
  width: 32px;
  display: block;
  padding: 0.5rem .3rem;
  text-decoration: none;
  border-radius: 0.1rem;
  border: 2px solid #d7d7d7; /* 2px */
  background-color: rgb(253, 255, 253);
  color: #131213;
  border-color: #111111;
  transform: scale(1.3,1.3);
}

.nav{
  z-index: 0;
  max-width: 800px;


}

